<template>
    <div class="home">
        <raw-loader :mytxt="require('!raw-loader!./TestLogin.vue').default"></raw-loader>
        <h1>Test login</h1>
        <b-row>
            <b-col md="2">
                <b-img
                    src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTv7CjqIxz7niAdt1MxlZM_iPsNAgGcnlKinuS-IiT42mwF-49Qi64wvAQLPvn2ecyL5Lw&usqp=CAU"
                    fluid alt="Fluid image"></b-img>
            </b-col>
            <b-col md="10">
                <!-- {{ token }} -->
                <div class="border-bottom pb-1 ">
                        <b-row>
                            <b-col md="7" class="border-right">
                                {{ user }}
                            </b-col>
                            <b-col md="5" class="p-1">
                                <div :class="{'border': false }">
                                    <div class="text-right pr-4" v-if=" token == '' ">
                                        username: <input v-model="acc.username" class="mb-1" /><br>
                                        password: <input v-model="acc.password" class="mb-1" /><br>
                                        <button @click="onLogin">Login</button>
                                    </div>
                                    <div v-else>
                                        <button @click="onLogout">Logout</button>
                                    </div>
                                </div>
                            </b-col>
                        </b-row>
                </div>
            </b-col>
        </b-row>

        <div class="border mt-1"> 
            <b-row> 
                <b-col md="4" class=""> 
                    <div class="border-right bg-light p-1"> 
                        <button @click="TestAdminOnly">TestAdminOnly</button>
                        <button @click="TestThemHs">TestThemHs</button>
                    </div>
                </b-col>
                <b-col md="8"> 
                    
                    Right 

                </b-col>
            </b-row>
        </div>


    </div>
</template>
<script>
import fetchClient from '@/fetchClient';
import TumLum from '@/TumLum';
export default {
    components: {
    },
    data(){
        return{
            acc:{
                username:'admin',
                password:'admin'
            },
            token:'',
            user:{},
        }
    },
    mounted(){
        let token = (localStorage.getItem("token")) ? localStorage.getItem("token") : '';
        this.token = token;
        this.GetUserInfo();
    },
    methods: {
        onLogout() {
            localStorage.removeItem("token");
            location.reload();
        },
        onLogin(){
            let user={
                Username:this.acc.username,
                Password:this.acc.password
            }
            this.Login(user);
        },
        async Login(r){
            return await fetchClient.post(`/api/NguoiDung5/Login`,r)
            .then(res=>{
                //alert(JSON.stringify(res));
                //return;
                alert(`Đăng nhập thành công.`);
                this.user.Username=res.data.kq.Username;
                this.user.Role=res.data.kq.Role;
                let token = res.data.kq.Token;
                localStorage.setItem("token", token);
                location.reload();
            }).catch(err=>{
                TumLum.XuLyErr(err);
            })
        },
        async GetUserInfo() {
            return await fetchClient.get(`/api/NguoiDung5/GetUserInfo`)
                .then(res => {
                    //alert(JSON.stringify(res));
                    /*
                    {"data":{
                        "user":{
                            "Id":1,
                            "FirstName":"admin",
                            "LastName":"Nguyễn",
                            "Username":"admin","Password":"","Role":"1,4,2,3","Token":null,"TrangThai":true},
                        "maxlevel":9,
                        "roles":
                            ["Admin","User","NhanVien","ChuQuan"],
                        "msg":"Get user info"},"status":200,"
                        "roles2":
                            ["Admin","User","NhanVien","ChuQuan"],"msg":"Get                        
                    */
                    this.user={
                        user:res.data.user,
                        maxlevel:res.data.maxlevel,
                        roles:res.data.roles,
                        roles2:res.data.roles2
                    };
                    //res.data.user;
                })
                .catch(err => {
                    TumLum.XuLyErr(err);
                })
        },
        async TestAdminOnly() {
            return await fetchClient.get(`/api/NguoiDung5/TestAdminOnly`)
                .then(res => {
                    //this.user=res.data.user;
                    alert(JSON.stringify(res));
                })
                .catch(err => {
                    TumLum.XuLyErr(err);
                })
        },
        async TestThemHs() {
            return await fetchClient.get(`/api/NguoiDung5/TestThemHs`)
                .then(res => {
                    //this.user=res.data.user;
                    alert(JSON.stringify(res));
                })
                .catch(err => {
                    TumLum.XuLyErr(err);
                })
        },


    }
}
</script>
  